.center{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.loader{
    border: 16px solid #f3f3f3;
    border-top: 16px solid dodgerblue;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
    position: relative;
    bottom: 180px;
}

@keyframes spin {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform:rotate(360deg);
    }
    
}